






























import {Component, Vue} from 'vue-property-decorator';
import {Cell, CellGroup, Col, Field, Icon, List as VantList, Row, Divider} from 'vant';
import 'vant/lib/icon/style';
import 'vant/lib/cell/style';
import 'vant/lib/cell-group/style';
import 'vant/lib/field/style';
import 'vant/lib/row/style';
import 'vant/lib/col/style';
import 'vant/lib/divider/style';
import 'vant/lib/list/style';
import {DiamondUtil} from '../../util/diamond_util';
import {CertOwn} from '../../model/certs_model';
import {ToastUtil} from "../../util/toast";

Vue.use(Icon).use(Field).use(Row).use(Col).use(CellGroup).use(Cell).use(VantList).use(Divider);
@Component
export default class List extends Vue {
    private cacheAllCertList: CertOwn[] = [];
    private showCertList: CertOwn[] = [];
    private loading = false;
    private finished = true;
    private certSearchNum = '';

    public created(): void {
        this.$store.state.title = this.$t('cert.list');
    }

    public mounted() {
        this.finished = false;      //每次进mounted，都加载一下
    }

    public searchCert(): void {
        window.console.log('begin search' + this.certSearchNum);
        if (this.cacheAllCertList.length < 1) { //本地没有证书信息列表,去获取
            ToastUtil.fail("证书列表加载为空");
            return;
        }
        let targetCert;
        for (let i = 0; i < this.cacheAllCertList.length; i++) {
            if (this.cacheAllCertList[i].certGia.gia && (this.cacheAllCertList[i].certGia.gia === this.certSearchNum)) {
                targetCert = this.cacheAllCertList[i] as CertOwn;
                break;
            }
        }
        if (targetCert !== null && targetCert !== undefined) {
            this.showCertList = [];
            this.showCertList.push(targetCert);
            this.$store.state.nowCertOwn = targetCert;
            return;
        } else {
            console.log(' typeof targetCert=>' + typeof targetCert);
            ToastUtil.fail("无相关的证书，请重新检查该gia证书，是否已完成上链");
        }
    }

    public onLoad(): void {
        // let nativeAddress = "5GrwvaEF5zXb26Fz9rcQpDWS57CtERHpNehXCPcNoHGKutQY"; //todo replace address alice
        DiamondUtil.getInstance().loadCertList(this.$store.state.nativeChainAddress).then((certs: CertOwn[]) => {
            this.loading = false;
            if (certs.length > 1) {
                this.cacheAllCertList = certs;
                this.showCertList = certs;
                this.finished = true;
            } else {
                ToastUtil.fail("证书列表加载为空");
                this.finished = true;
            }
        }).catch((e: any) => {
            this.loading = false;
            this.finished = true;
            ToastUtil.fail("数据加载失败" + e.toString());
        });
    }

    public onClickRow(id: string): void {
        const certOwn = this.showCertList.find((it) => (it.certGia.gia === id));
        if (certOwn) {
            console.log("certOwn.cert.id" + id);
            this.$store.state.nowCertOwn = certOwn;
            this.$router.push('/frame/cert/detail');
        }
    }
}
